nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: rgb(35, 32, 32);
  color: white;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 70px;
  /* padding-right: 50px; */
  z-index: 1000;
}
.responsive-nav{
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.logo{
  height: 100%;
  width: 20%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.logo img{
  height: 40px;
  width: 40px;
  /* background-color: green; */
}
.logo img:hover{
  height: 50px;
  width: 50px;
  cursor: pointer;
}

.nav-contents {
  /* background-color: red; */
  width: 40%;
  height: 100%;
  display: flex;
  justify-content:space-evenly;
  align-items: center;
  /* gap: 40px; */
}



.nav-contents-contents{
  cursor: pointer;
}

.nav-contents-contents:hover{
  font-size:20px;
  color: rgb(188, 245, 179);
  }
  

