          animation: html;
          animation-duration: 6s;
        }
        @keyframes html {
          from {width: 0px;}
          to {width: 90%;}
        }
    
        .know-bar-css{
          background-image: linear-gradient(to bottom right,rgb(253, 220, 252), rgb(224, 165, 206) ,rgb(189,235,228));
          height: 100%;
          width: 80%;
          border-radius: 50px;
          animation: css;
          animation-duration: 6s;
        }
        @keyframes css {
          from {width: 0px;}
          to {width: 80%;}
        }
    
        .know-bar-js{
          background-image: linear-gradient(to bottom right,rgb(253, 220, 252), rgb(224, 165, 206) ,rgb(189,235,228));
          height: 100%;
          width: 70%;
          border-radius: 50px;
          animation: js;
          animation-duration: 6s;
        }
        @keyframes js {
          from {width: 0px;}
          to {width: 70%;}
        }
    
        .know-bar-rjs{
          background-image: linear-gradient(to bottom right,rgb(253, 220, 252), rgb(224, 165, 206) ,rgb(189,235,228));
          height: 100%;
          width: 70%;
          border-radius: 50px;
          animation: rjs;
          animation-duration: 6s;
        }
        @keyframes rjs {
          from {width: 0px;}
          to {width: 70%;}
        }
        
        .know-bar-bs{
          background-image: linear-gradient(to bottom right,rgb(253, 220, 252), rgb(224, 165, 206) ,rgb(189,235,228));
          height: 100%;
          width: 87%;
          border-radius: 50px;
          animation: bs;
          animation-duration: 6s;
        }
        @keyframes bs {
          from {width: 0px;}
